<div class="tutorial"><div class="ui container" id="tutorial-container"><h2 class="ui header">新手任務</h2><div class="progress-container"><progress class="ui progress" :value="!someTaskCompleted[0] ? 0 : (someTaskCompleted || []).filter((b) =&gt; { return b}).length * 25" max="100"></progress></div><div class="ui left aligned list"><div class="item center aligned" v-for="(task, index) in tasks" :key="index"><router-link :to="task.route" v-if="index == 0 || someTaskCompleted[0]">任務{{index+1}}： {{ task.name }}</router-link><span class="gray" v-else>任務{{index+1}}： {{ task.name }}</span><span v-show="someTaskCompleted[index] &amp;&amp; someTaskCompleted[0]"> ✔️</span></div></div></div><button class="ui button primary" v-if="allTasksCompleted" @click="hideTutorial"><i class="hide icon"></i>隱藏新手任務</button></div>